#signBack{
    background: linear-gradient(to top right, #0078c9, #00c59a);
    height: 100vh;
    width: 100vw;
}

#form{
    background-color: #fff;
    position: absolute;
    width: 400px;
    box-shadow: #696969 0 0 20px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    padding: 20px 30px;
    text-align: center;
    border-radius: 5px;
}
#form h2{
    font-size: 30px;
    margin: 10px;
}
#form h3{
    font-size : 25px;
    font-weight: 700;
}


.cusButton{
    background-color: transparent;
    border: 1px solid #00c59a;
    color: #00c59a;
    width: 90%;
    padding: 13px;
    transition: all .2s ease-in;
    margin: 10px auto;
}

.cusButton:hover{
    background-color:#00c59a;
    color: #fff;
    cursor: pointer;
    box-shadow: #00c59a79 0 3px 10px;
}
.cusButton:active{
    color: #fff;
}
.cusButton:focus{
    color: #fff;
}
#withGoogle{
    background-color: #DB4437;
    border: 1px solid #DB4437;
    color: #fff;
}

#withGoogle:hover{
    background-color:transparent;
    color: #DB4437;
    box-shadow: #DB443779 0 3px 10px;
}

@media only screen and (max-width: 420px){
    #form{
        width: 300px
    }
}
@media only screen and (max-width: 300px){
    #form{
        width: 220px
    }
}